.pokelist__item {
    width: 130px;
    text-align: center;
    -webkit-transition: var(--transition);
    -o-transition: var(--transition);
    transition: var(--transition);
    border-radius: 4px;
    margin-bottom: 10px;
    padding-bottom: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.pokelist__item:hover {
    -webkit-box-shadow: 0px 0px 5px 1px #00000085;
            box-shadow: 0px 0px 5px 1px #00000085;
    cursor: pointer;
}

.pokelist__sprite {
    max-width: 80px;
    margin: 0 auto 10px;
}


